.chat-window {
	width: 300px;
	height: 420px;
}

.chat-window p {
	margin: 0;
}
.chat-window .chat-header {
	height: 45px;
	border-radius: 6px;
	background: #263238;
	position: relative;
	cursor: pointer;
}
.chat-window .chat-header p {
	display: block;
	padding: 0 1em 0 2em;
	color: #fff;
	font-weight: 700;
	line-height: 45px;
}

.chat-window .chat-footer {
	height: 40px;
	border: 1px solid #263238;
	border-top: none;
	display: flex;
}

.chat-window .chat-footer input {
	height: 100%;
	flex: 85%;
	border: 0;
	padding: 0 0.7em;
	font-size: 1em;
	border-right: 1px dotted #607d8b;

	outline: none;
	font-family: 'Open Sans', sans-serif;
}

.chat-window .chat-footer button {
	border: 0;
	display: grid;
	place-items: center;
	cursor: pointer;
	flex: 15%;
	height: 100%;
	background: transparent;
	outline: none;
	font-size: 25px;
	color: lightgray;
}

.chat-window .chat-footer button:hover {
	color: #43a047;
}
.hide {
	opacity: 0 !important;
}

.exitBtn {
	width: 100%;
	display: flex;
	justify-content: center;
	margin-top: 10px;
}

.exitBtn button {
	background-color: red;
	border-radius: 4px;
	padding: 5px 10px;
	color: #fff;
	cursor: pointer;
}

.exitBtn .exitBtn button:hover {
	opacity: 20%;
}
